@use "sass:math";
@import 'gridstack/dist/gridstack.min.css';
@import 'ag-grid-community/styles/ag-grid.css';
@import 'ag-grid-community/styles/ag-theme-quartz.css';

:root{
  /* Layout sizes */
  --topbar-primary-h: 46px;   /* slightly slimmer than full */
  --topbar-secondary-h: 50px;
  --sidebar-w: 268px;         /* slightly wider */
  --sidebar-collapsed-w: 56px;
  --footer-h: 40px;
  --page-pad: 24px;
  --canvas-gap: 16px;

  /* Colors (Ocean Blue Serenity) */
  --primary-900: #023E8A;     /* dark navy */
  --primary-700: #0077B6;
  --primary-500: #0096C7;
  --primary-300: #48CAE4;
  --bg-weak: #F9F9F9;
  --surface: #ffffff;
  --text: #202020;
  --muted: #6b7280;
  --ring: #e5e7eb;
  --sidebar-bg: #FBFBFD;      /* soft light like ClickUp */
  --sidebar-label: #111827;   /* strong text */
  --sidebar-muted: #6b7280;   /* secondary */

  /* Zoom tokens */
  --zoom-scale: 1;
  --zoom-origin-x: 0px;
  --zoom-origin-y: 0px;
}
*{box-sizing:border-box}
html,body{height:100%}
body{
  margin:0;
  font-family:system-ui,-apple-system,"Inter","Segoe UI",Roboto,Arial;
  color:var(--text);
  background:var(--bg-weak);
}

/* App grid: two topbars + content + footer */
.app-frame{
  display:grid;
  grid-template-rows: var(--topbar-primary-h) var(--topbar-secondary-h) 1fr var(--footer-h);
  grid-template-columns: var(--sidebar-w) 1fr;
  /* secondary topbar aligns with canvas/main column only */
  grid-template-areas:
    "topbar-primary topbar-primary"
    "sidebar        topbar-secondary"
    "sidebar        main"
    "footer         footer";
  height:100dvh;
  transition:grid-template-columns .18s ease;
}
.app-frame.collapsed{
  grid-template-columns: var(--sidebar-collapsed-w) 1fr;
}

.topbar-primary{
  grid-area:topbar-primary;
  background:linear-gradient(90deg,var(--primary-900),var(--primary-700));
  color:#fff;
  display:flex;
  align-items:center;
  gap:12px;
  padding:0 12px;
}
.topbar-secondary{
  grid-area:topbar-secondary;
  background:var(--surface);
  border-bottom:1px solid var(--ring); /* gray divider under the page bar */
  display:flex;
  align-items:center;
  gap:12px;
  padding:0 12px;
}
.sidebar{
  grid-area:sidebar;
  background:var(--sidebar-bg);
  border-right:1px solid var(--ring);
  overflow:auto;
}
.main{
  grid-area:main;
  padding:var(--page-pad);
  overflow:auto;
}
.footer{
  grid-area:footer;
  background:var(--surface);
  border-top:1px solid var(--ring);
  display:flex;
  align-items:center;
  justify-content:space-between;
  padding:0 12px;
  color:var(--muted);
  font-size:12px;
}

/* Canvas */
.canvas-scroll{overflow:auto;height:100%}
.canvas{
  transform-origin:top left;
  transform:translate(var(--zoom-origin-x),var(--zoom-origin-y)) scale(var(--zoom-scale));
  min-width:1200px;  /* baseline; safe for first run */
  min-height:900px;
  display:grid;
  gap:var(--canvas-gap);
  padding:var(--canvas-gap);
  background:transparent;
}

/* Sidebar nav */
.nav{
  padding:12px 12px 16px 12px;
}
.nav .sec{
  margin:14px 0 8px;
  font-size:11px;
  text-transform:uppercase;
  letter-spacing:.4px;
  color:var(--muted);
  padding:0 8px;
}
.nav a{
  display:flex;
  align-items:center;
  gap:12px;
  padding:10px 12px;
  border-radius:12px;
  color:var(--sidebar-label);
  text-decoration:none;
  font-size:14.5px;
}
.nav a:hover{background:#f3f4f6;}
.nav a.active{
  background:#ecefff;
  color:var(--primary-900);
  font-weight:600;
}
.icon{
  width:22px;
  height:22px;
  display:inline-grid;
  place-items:center;
  border-radius:8px;
  background:#eef2ff;
  color:var(--primary-900);
  font-weight:700;
  font-size:12px;
}
